import React, { useEffect } from 'react';

//import npm packages
import AOS from 'aos';

//import styles
import * as classes from "./bottomtext.scss";
import mesa from "../../../../assets/icons/mesa-nurol.png";
import nur from "../../../../assets/images/nurol.svg";
import { Swiper, SwiperSlide } from "swiper/react";


const BottomText = () => {

    useEffect(() => {

        AOS.init()

    }, [])

    return (
        <main>
            <div className="imgwrap" data-aos={'zoom-in'} data-aos-duration={'1300'} style={{ marginLeft: 25 }}>
                <div className="classes.imgbox">
                    <img
                        className="d-block"
                        src={mesa}
                        alt="mesa"
                        width={'100%'}
                    />
                </div>
            </div>

            <Swiper
                spaceBetween={12}
                centeredSlides={true}
                pagination={true}
                slidesPerView={'auto'}
                autoplay={{
                    "delay": 6000,
                    "disableOnInteraction": false
                }}
                className="bottomSwiper"
            >
                <SwiperSlide
                    className="bottomSlide"
                >
                    <p data-aos={'flip-down'} data-aos-duration={'1300'}>
                        50 yl akn sredir sadece<br />
                        yeni yuvalar iin deil,<br />
                        yeni hatralar biriktirmek iin de alyoruz.<br />
                        nk biz insann biriktirebilecei <br />
                        en deerli eyin anlar olduuna inanyoruz.

                    </p>
                </SwiperSlide>
                <SwiperSlide
                    className="bottomSlide"
                >
                    <p data-aos={'flip-down'} data-aos-duration={'1300'}>
                        Bugne kadar hayata geirdiimiz yzlerce projeye deer katan,<br />
                        bizi byk bir aile yapan yz binlerce dostumuzun<br />
                        en gzel anlarnn ortak noktas olmann mutluluunu yayoruz.<br />
                        imdi de gzel ve unutulmaz anlarnza yenilerini ekleyeceiniz,<br />
                        muhteem doasyla kendinizi hep gen hissedeceiniz<br />
                        Yeilyakay beeninize sunuyoruz.<br />
                    </p>
                </SwiperSlide>
            </Swiper>
        </main>
    )
}

export default BottomText;