import React, { useEffect } from 'react';

//import npm packages
import AOS from 'aos';

//import Gatsby
import { Link } from 'gatsby';

//import images
import { conceptSwiper, indexFlow } from "../../../../helper/IndexData";

//import styles
import * as classes from "./indexnature.module.scss";
import SmallTitle from "../../../../atoms/SmallTitle";

const IndexNature = () => {


    return (
        <>
            <section className={classes.nature}>
                <div className={classes.natureText} data-aos={'fade-down'} data-aos-duration={'1300'}>
                    <SmallTitle title="DOAYI YELYAKA'DA YAA" />
                    <h2>Dubleks ve Tek Katl
                        Baheli Villalar</h2>
                    <p>
                        stanbul grltsnden uzak, orman ile evrelenmi villalarda her annz tatilde gibi yaayabilirsiniz.
<br />
                        Ulam, sosyal hayat ve ihtiyalarnz iin doadan uzaklamaya gerek kalmadan evinizde huzuru bulabilirsiniz.
<br />
                        stanbulda lks bir hayat standardna ulamak, kaliteli yaamak iin doa ve ehir Yeilyakada.
<br />
                    </p>
                    <Link to={'/iletisim'}><button>imdi Bilgi Aln</button></Link>
                </div>
                <div className={classes.natureImage} data-aos={'zoom-in'} data-aos-duration={'1300'}>
                    <div className={classes.imgWrap}>
                        <img src={indexFlow[0].img} alt={indexFlow[0].alt} />
                    </div>

                </div>
            </section>
        </>
    )
}
export default IndexNature;